@font-face {
  font-family: Leafy;
  src: url(./leafy.woff); }

p, h1, h2, h3, h4, b {
  font-family: "Leafy"; }

body {
  margin: 0;
  padding: 0;
  background-color: #EEEEEE; }

.navbar {
  position: fixed;
  width: 100%;
  background-color: #212121;
  padding: 5px 0px;
  z-index: 1; }
  .navbar .back-button {
    position: absolute;
    text-align: justify;
    color: white;
    top: 20px; }
  .navbar h2 {
    text-align: center;
    color: white; }

.page-container {
  padding: 20px 30px;
  padding-top: 10vh;
  position: relative;
  line-height: 20px; }

.page-footer {
  position: fixed;
  background-color: #212121;
  color: white;
  width: 100vw;
  bottom: 0; }

.card {
  color: #212121;
  margin: 10px; }

.generic-button {
  position: relative;
  margin: 10px;
  font-size: 18px;
  width: 90%;
  -webkit-box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185);
          box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185);
  background-color: white; }

.rmc-dialog-body input.code-input {
  font-family: "Leafy";
  font-size: 30px;
  line-height: 36px;
  border-radius: 20px;
  padding-left: 10px;
  padding-bottom: 10px; }



