@font-face {
  font-family: Leafy;
  src: url(./leafy.woff); }

.create {
  text-align: center;
  font-family: "Leafy"; }
  .create input {
    font-size: 29px;
    line-height: 20px; }
  .create .rc-switch-checked {
    border: 1px solid #212121;
    background-color: #212121; }
  .create .create-select-rounds {
    text-align: right; }
    .create .create-select-rounds li {
      margin: 10px 5px; }
      .create .create-select-rounds li .round-checkbox {
        margin: 0px 10px;
        right: 0px; }



