@font-face {
  font-family: Leafy;
  src: url(./leafy.woff); }

.bowl .word-input {
  font-family: "Leafy";
  border: 1px solid transparent;
  padding: 0px 5px; }
  .bowl .word-input:focus {
    outline: none;
    -webkit-box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185);
            box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185); }

.bowl .words-container .words > .remove-words-btn:hover, .bowl .words-container .words > .remove-words-btn:focus {
  border: none;
  outline: none; }

.bowl {
  text-align: center;
  font-family: "Leafy"; }
  .bowl .words-container {
    display: inline-block; }
    .bowl .words-container .words {
      -webkit-animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
              animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
      display: -ms-flexbox;
      display: flex; }
      .bowl .words-container .words > .remove-words-btn {
        font-family: "Leafy";
        font-weight: bold;
        background: none;
        border: none;
        color: black; }
  .bowl .word-input {
    border-radius: 10px;
    font-size: 20px;
    line-height: 30px;
    -webkit-box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185);
            box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185); }
  .bowl .word-submit {
    font-size: 20px;
    height: 30px;
    border-radius: 10px;
    -webkit-box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185);
            box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185); }
    .bowl .word-submit.disabled-btn {
      opacity: 0.8;
      background-color: grey; }

.page-footer .foot-header {
  text-align: justify;
  margin-left: 10px;
  line-height: 20px; }

@-webkit-keyframes slide-in-left {
  0% {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1; } }

@keyframes slide-in-left {
  0% {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1; } }



