@font-face {
  font-family: Leafy;
  src: url(./leafy.woff); }

@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0; }
  40% {
    opacity: .6; }
  100% {
    opacity: 1; } }

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -.5em;
    opacity: 0; }
  40% {
    opacity: .6; }
  100% {
    opacity: 1; } }

@-webkit-keyframes tracking-out-contract {
  0% {
    opacity: 1; }
  50% {
    opacity: 1; }
  100% {
    letter-spacing: -.5em;
    opacity: 0; } }

@keyframes tracking-out-contract {
  0% {
    opacity: 1; }
  50% {
    opacity: 1; }
  100% {
    letter-spacing: -.5em;
    opacity: 0; } }

.game {
  text-align: center;
  font-family: "Leafy"; }
  .game .game-type {
    padding-top: 5%;
    font-size: 50px;
    line-height: 60px; }
  .game .game-timer {
    padding-top: 5%; }
    .game .game-timer .game-time-seconds {
      font-size: 100px; }
  .game .game-word {
    font-size: 70px;
    line-height: 200px; }
    .game .game-word .enter {
      -webkit-animation: tracking-in-expand .12s linear both;
      animation: tracking-in-expand .12s linear both; }
    .game .game-word .leave {
      -webkit-animation: tracking-out-contract .12s linear both;
      animation: tracking-out-contract .12s linear both; }
  .game .game-selection {
    padding-top: 90px; }
    .game .game-selection .player-btn {
      line-height: 50px;
      width: 200px;
      font-family: "Leafy";
      font-size: 29px;
      border-radius: 20px; }



